import React from 'react';
import {Component} from 'react/cjs/react.production.min';

export class DialogComment extends Component {
    render() {
        return this.props.isActive ? (
            <div class="dialog-bg align-items-center">
                <div class="dialog">
                    <h2 class="dialog-title">Kommentare verfassen</h2>
                    <p>Um ein Kommentar zu verfassen musst du dich zunächst Registieren!</p>

                    <button disabled class="btn btn-primary">
                    Registrieren</button>
                    <button class="btn dialog-close-btn"
                        onClick={
                            () => {
                                this.props.setState({commentDialog: false})
                            }
                    }>Schließen</button>
                </div>
            </div>
        ) : <></>
    }
}


export class DialogContact extends Component {
    render() {
        return this.props.isActive ? (
            <div class="dialog-bg align-items-center">
                <div class="dialog">
                    <h2 class="dialog-title">Kontaktieren</h2>
                    <p>Derzeit nehme ich keine weiteren Projekte an!

                    </p>
                    <button class="btn dialog-close-btn"
                        onClick={
                            () => {
                                this.props.setState({commentDialog: false})
                            }
                    }>Schließen</button>
                </div>
            </div>
        ) : <></>
    }
}
